@import url(https://fonts.googleapis.com/css2?family=Muli:wght@200;300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: "Muli", sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

@media screen and (min-width: 1000px) {
  .App {
    margin: auto;
    max-width: 50%;
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.8), 0 2px 5px 0 rgba(0, 0, 0, 0.6);
  }
}

.textLabel {
  font-size: 15px;
  margin-bottom: 0.3rem;
}

.textLabelBold {
  font-size: 15px;
  font-weight: 700;
  margin-bottom: 0.3rem;
}

.textLabelFaded {
  font-size: 15px;
  color: gray;
  margin-bottom: 0.3rem;
}

.textLabelBlue {
  font-size: 15px;
  color: rgb(11, 166, 255);
}

.textLabelBlueFaded {
  color: rgb(151, 217, 255) !important;
}

.unselectableText {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.feedContainer {
  margin-bottom: 3rem;
}

.navContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 0.25rem 0;
    background-color: rgb(248, 248, 248);
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.8), 0 2px 5px 0 rgba(0, 0, 0, 0.6);
}

.navIcon {
    width: 33px;
    height: 33px;
}


.postContainer {
  display: flex;
  flex-direction: column;
}

.postHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  padding: 0.6rem 0.9rem;
}

.userInfo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.username {
  margin-left: 0.6rem;
  font-weight: 600;
}

.menuIcon {
  width: 1.5rem;
  height: 1.5rem;
}

.lineSeparator {
  border: 0;
  background-color: rgb(230, 230, 230);
  height: 1px;
  width: 100%;
  padding: 0;
  margin: 0;
}

.utilsContainer {
  display: flex;
  flex-direction: column;
  padding: 0.6rem 0.9rem;
}

.contentCanvas {
  width: 100%;
  margin: 0;
  padding: 0;
}

.actionsContainer {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-bottom: 0.6rem;
}

.actionIcon {
  width: 30px;
  height: 30px;
  transition: -webkit-transform 100ms;
  transition: transform 100ms;
  transition: transform 100ms, -webkit-transform 100ms;
}

.shareIcon {
  margin-left: 0.6rem;
}

.bookmarkIcon {
  margin-left: auto;
  color: rgb(255, 62, 62);
}

.actionIcon:active {
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
}



.commentsLink {
  text-decoration: none;
  margin-bottom: 0.3rem;
}

.postComment {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.commentTextBox {
  border: 0;
  outline: none;
  width: 75%;
}

.commentContainer {
  margin-bottom: 0.3rem;
}








.inputForm {
    display: flex;
    flex-direction: column;
    align-self: center;
    justify-self: center;
}

